body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
}
ol,li,ul{list-style: none;}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
#login_form{
  width: 340px;
  margin: 0px auto;
  text-align: center;
  height: 460px;
  
}
#id,#pw{
  width: 340px;
  height: 56px;
  margin-bottom: 5px;
  border-radius: 5px;
  border: 1px solid #ccc;
  color:#333;
  font-size: 14px;
}
#login{
  width: 340px;
  height: 56px;
  margin-top: 15px;
  background: #5F0080;
  color:#fff;
  border: 0px ;
  border-radius: 5px;
  font-weight: bold;
  font-size: 16px;
  cursor: pointer;
}
.find{
  font-size: 13px;
  float: right;
  color:#333;
}
#join{
  width: 340px;
  height: 56px;
  margin-top: 15px;
  background: #fff;
  color:#5F0080;
  border: 1px solid #5F0080;
  border-radius: 5px;
  font-weight: bold;
  font-size: 16px;
  cursor: pointer;
}
h3{text-align: center;}
.product_list{
  width: 1050px;
  margin: 0px auto;
  display: flex;
  justify-content:space-evenly;
}
.product_list li{
  width: 249px;height: 434px;
  position: relative;
}
.product1{
  margin-bottom: 100px;
}
.product_list img{
  width: 100%;
  height: 320px;
}
.name1{
  margin-top: 10px;
}
.price{
  font-weight: bold;
  margin-top: 10px;
}
.icon{
  background: #5F0080;
  color:#fff;
  border-radius: 50%;
  width: 35px;
  height: 35px;
  display: block;
  line-height: 35px;
  text-align: center;
  position: absolute;
  right:10px;
  bottom:27%;
  cursor: pointer;
}
