@charset "utf-8";
*{margin:0px;padding: 0px;}
ul,li,ol{list-style: none;}
body{
    font-family: "맑은 고딕",arial, sans-serif;
    color:#333;
    overflow-x: hidden;
}
a{text-decoration: none;color: #333;}

header{
    width: 100%;
    height: 156px;
    box-shadow: 0px 0px 3px #333;
}
.header_inner{
    width: 1050px;
    margin: 0px auto;
    text-align: center;
    height: 100px;
    position: relative;
}
h1{
    position: absolute;
    top:40%;
}
.main{
    width: 100%;
    
}
.product{
    width: 100%;
    height: 600px;
    margin: 0px auto;
}
.product_list li:first-child:hover{
    transform: scale(1.01);
}
.as{
    margin-top: 30px;
}

.search{
    width: 400px;
    height: 48px;
    border: 1px solid #5F0080;
    color:#333;
    border-radius: 10px;
    position: absolute;
    top:35%;left:30%;
    font-size: 14px;
}
.search_ico{
    font-size: 24px;
    position: absolute;
    color:#5F0080;
    top:47%; left: 65%;
    cursor: pointer;
}
.search:focus{
    outline: #5F0080;
}
.ico{
    display: flex;
    font-size: 32px;
    position: absolute;
    top:35%;right: 10px;
    color:#5F0080
    
}
.ico li{
    margin-left: 10px;
    cursor: pointer;
}
.gnb{
    display: flex;
    font-size: 12px;
    position: absolute;
    top:10%;right: 10px;
    content: "";
}
.gnb li{
    padding-right: 10px;
}
.gnb >li:first-child{
    color:#5F0080;
}
.sub_m{
    width: 1920px;
    background: #fff;
    height: 56px;
    position:fixed;
    right: -17px;top: 100px;
    font-size: 16px;
    font-weight: bold;
    z-index: 1000;
}
.sub_m span{
    position: absolute;
    top:35%;left:430px;
}
.sub_mnu{
    display: flex;
    width: 600px;
    justify-content: space-between;
    position: absolute;
    top:35%;left:35%;
}
.fixed{
    top:0px;
}

  ul,ol,li{
    list-style: none; 
  }
  .slide-wrapper{
    width: 100%;height: 373px;
    margin: 0px auto;
    position: relative;
    margin-bottom: 20px;
  }
  .slide{
    display: flex;
    width: 1620px;
    position: absolute;
    left: 0px;top:0px;
    transition: 0.3s;
  
  }
  .slide > li{
    width: 1920px;
  }
  .slide > li img{
    width: 100%;
    margin: 0px auto;
  }
  .slide > li:not(:last-child){
    margin-right: 30px;
  }
  .slide > li > div{}
  .btn_group{
  }
  .btn_group > input[type=button]{}
  .btn_group .prev,.btn_group .next{
    width: 40px;
    height: 50px;
    background: #ccc;
    position: absolute;
    top:40%;
    cursor: pointer;
    border: 0px;
  }
  .btn_group .prev{
    left:0px;
  
  }
  .btn_group .next{
    right: 0px;
  }
.banner{
   width: 1050px;
    margin: 0px auto;
}
.banner img{
    width: 100%;
}
.banner{
    padding-bottom: 50px; 
}
footer h2{
    margin-bottom: 20px;
}
.tel{
    font-size: 28px;
}
.time{
    font-size: 16px;
}
.top{
    width: 50px;
    height: 50px;
    background: #fff;
    border-radius: 50%;
    border:1px solid #333;
    color:#333;
    display: block;
    position: fixed;
    bottom:20px;right: 0px;
    text-align: center;
    line-height: 45px;
    cursor: pointer;
    transition: 0.5s;
}
footer{
    width: 100%;
    height: 580px;
    
    border: 1px solid #ccc;
    
}
.f_inner{
    width: 1050px;
    margin: 0px auto;
    height: 580px;
    position: relative;
}
address{
    width: 100%;
    height: 102px;
    text-align: center;
    background: #ccc;
    font-style: normal;
}
.company{
    width: 600px;
    display: flex;
    font-size: 14px;
    font-weight: bold;
    position: absolute;
    right: 0px;
}
.company li{
    margin-right: 10px;
    
}
.show{
    font-size: 12px;
    position: absolute;
    width: 600px;
    top:30%;right: 0px;
    color:rgb(120, 118, 118);
}
.show a{
    color:#5F0080;
}
.notf{
    padding-top: 50px;
    width: 1050px;
    height: 500px;
    margin: 0px auto;
    text-align: center;
    font-size: 24px;
}
.home{
    width: 100px;
    height: 50px;
    background: #5F0080;
    color:#fff;
    border:none;
    border-radius: 10px;
    cursor: pointer;
    margin-top: 20px;
}
h3{
    margin-bottom: 10px;
}

.sale{
    color:#f00;
    font-weight: bold;
    position: absolute;
    bottom:20px;
    left:0px;
}
.price{
    position: absolute;
    bottom: 20px;
    left:40px;
}

.sale2{
    color:#ccc;
    position: absolute;
    bottom: 0px;
}